import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastController, LoadingController, AlertController } from '@ionic/angular';
import { AccessProviders } from '../../providers/access-providers';
import { HttpClient } from '@angular/common/http';
import { from, Observable, Subscriber } from 'rxjs';

@Component({
  selector: 'app-modificarprov',
  templateUrl: './modificarprov.page.html',
  styleUrls: ['./modificarprov.page.scss'],
})
export class ModificarprovPage implements OnInit {

  id: number;
  tipo: string="";
  nombreali: string="";
  cantidad_pro: string="";
	cantidad_min: string="";
	cantidad_med: string="";
	cantidad_max: string="";


  disabledbutton;


  constructor(
    private router: Router,
    private toastCtrl: ToastController,
    private alertCtrl: AlertController,
    private loadingCtrl: LoadingController,
    private accsPrvds: AccessProviders,
    private actRoute: ActivatedRoute
  ) { }

  ngOnInit() {
    this.actRoute.params.subscribe((data: any)=>{
      console.log(data);
      this.id = data.id;

      if(this.id != 0){
        this.cargarMenu()
      }
    });
  }

  cargarMenu(){
    return new Promise(resolve =>{
      let body = {
        aksi: 'load_single_dataPV', 
        id: this.id
      }
      this.accsPrvds.postData(body, 'proses_api.php').subscribe((res: any) => {
        this.tipo = res.result.tipo;
        this.nombreali = res.result.nombreali;
        this.cantidad_pro = res.result.cantidad_pro;
        this.cantidad_min = res.result.cantidad_min;
        this.cantidad_med = res.result.cantidad_med;
        this.cantidad_max = res.result.cantidad_max;
      });
    });
  }


  async crudActionguardar(a){
    if(this.tipo=="",
      this.nombreali=="",
      this.cantidad_pro=="",
      this.cantidad_min=="",
      this.cantidad_med=="",
      this.cantidad_max==""
    ){
 
    }else{
      this.disabledbutton = true;
      const loader = await this.loadingCtrl.create({
        message: 'Espere por favor....',
      });
      await loader.present();

      return new Promise(resolve =>{
        let body = {
          aksi: 'mod_crudPV',
          id: this.id,
          tipo: this.tipo,
          nombreali: this.nombreali,
          cantidad_pro: this.cantidad_pro,
          cantidad_min: this.cantidad_min,
          cantidad_med: this.cantidad_med,
          cantidad_max: this.cantidad_max,
          action: a
        }

        this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
          if(res.success==true){
            loader.dismiss();
            this.disabledbutton = false;
            this.presentToast(res.msg);
            this.router.navigate(['/menu-admin']);
          }else{
            loader.dismiss();
            this.disabledbutton = false;
            this.presentAlert(res.msg, a);
          }
        },(err)=>{
          loader.dismiss();
          this.disabledbutton = false;
          this.presentAlert('Timeout',a);
        });
      });
    }
  }

  
  async presentToast(a){
    const toast = await this.toastCtrl.create({
      message: a,
      duration: 1500,
    });
    toast.present();
  }

  async presentAlert(a,b) {
    const alert = await this.alertCtrl.create({
      header: a,
      backdropDismiss: false,
      buttons: [
        {
          text: 'Close',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
          }
        }, {
          text: 'Try Again',
          handler: () => {
            this.crudActionguardar(b);
          }
        }
      ]
    });

    await alert.present();
  }

}
